.main-container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  height: 100vh;
  @media only screen and (min-width: 1024px) {
    max-width: 1024px;
  }
  @media only screen and (min-width: 1280px) {
    max-width: 1280px;
  }
  @media only screen and (max-width: 600px) {
    max-width: 600px;
  }
  @media only screen and (max-width: 300px) {
    max-width: 300px;
    padding: 0 30px;
  }
}

.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  .wrap {
    width: 360px;
  }
}

.main-container.position {
  position: relative;
}

.main-container.active {
  padding: ($height-header + 15px) 20px;
}

.container-page {
  margin-top: 50px;
  height: inherit;

}


.main{
  margin-left: auto;
  margin-right: auto;
}