.navbar {
  display: flex;
  justify-content: space-between;
  padding: 2px 50px;
  align-items: center;
  height: inherit;

  .logo {
    font-size: 3rem;
    font-family: 'Kaushan Script', cursive;
    color: $light;
  }
  .nav-right {
    font-size: 1rem;
    display: flex;
    ul {
      display: flex;
      list-style: none;

      li {
        margin-left: 10px;
        margin-right: 10px;
        background-color: #f0f8ff0a;
        padding: 10px 25px;
        border-radius: 5px;
        transition: all 200ms ease-in-out;
        &:hover {
          background-color: $light;
        }

        &:hover > a {
          color: #0c0c22;
        }
        a {
          height: auto;
          width: auto;
          text-decoration: none;
          opacity: 1;
          color: $light;
        }
      }
      @media only screen and (max-width: $width-tablet-ipad){
        display: none;
      }
    }
  }
}

.icon {
  display: flex;
  cursor: pointer;

  &:hover {
    color: $primary;
  }
}

.submenu {
  padding: 15px 0;
  min-width: 200px;
  position: relative;
}

.nav-right-modal {
  padding: 10px 2px;

  min-width: 175px;
  .settings-header,
  .settings-body,
  .settings-footer {
    cursor: pointer;
  }
  .settings-header,
  .settings-footer > span,
  strong {
    display: block;
  }
  span,
  strong {
    padding: 0px 15px;
  }
  & > a {
    display: block !important;
    color: inherit;
    text-decoration: none;
  }
  .settings-header {
    padding: 10px 0;
    border-bottom: 0.5px #70707045 solid;

    &:hover {
      background-color: $color-hover;
    }
  }

  .settings-body {
    border-bottom: 0.5px #70707045 solid;
    span {
      display: block;
      padding: 5px 15px;
      &:hover {
        background-color: $color-hover;
      }
    }
  }
  .settings-footer {
    padding: 10px 0;

    &:hover {
      background-color: $color-hover;
    }
  }
}

.submenu-modal {
  position: relative;
  & > div {
    right: 0;
    top: 55px;
  }
}

.button-active {
  color: #0a4769 !important;
  background-color: $light !important;
}
